
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
  transition: all .5s;
  display: flex;
  justify-content: center;
  align-items:center;
  width: 100%;
  height: auto;
}

.page{
  min-width: 1920px;
  height: 1041px;
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.one{
  min-width: 543px ;
  min-height: 633px;
  background: url(./comp/bg2.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  /* margin-left: 100px;
   */
   margin-left: 5%;
  /* border: 1px solid black; */
}
.two{
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
  /* margin-left: 100px; */
  margin-left: 5%;
  min-height: 961px;
  min-width: 1047px ;
  border: 1px solid rgb(30, 30, 30,.8);
}
#firsth1{
  margin-top: 18%;
  width: 133px;
  height:53px;
  display: flex;
  font-size: 48px;
  justify-content: center;
  align-items: center;
  /* border: 1px solid black; */
}
/* 543 633      1047 961 */

.input_s{
  width: 835px;
  height: auto;
